'use client'
import Head from 'next/head'
import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import apiClient from '../../lib/api-client'
import dynamic from 'next/dynamic'

// load GanttChart lazily (client-only)
const GanttChart = dynamic(() => import('../../components/GanttChart'), { ssr: false })

export default function Dashboard(){
  const router = useRouter()
  const [user, setUser] = useState('Pengguna')
  const [loading, setLoading] = useState(false)
  const [stats, setStats] = useState({ total:0, new:0, assigned:0, ready_to_deploy:0, deployed:0, in_progress:0, completed:0, overdue:0 })
  const [upcoming, setUpcoming] = useState([])
  const [overdueList, setOverdueList] = useState([])
  const [lastUpdated, setLastUpdated] = useState(null)

  useEffect(()=>{
    if (typeof window === 'undefined') return
    const auth = localStorage.getItem('sigap_auth')
    if (!auth){ router.push('/login'); return }
    setUser(localStorage.getItem('sigap_user') || 'Pengguna')
    loadDashboard()
    const id = setInterval(() => loadDashboard(), 60*1000)
    return () => clearInterval(id)
  }, [router])

  function logout(){
    localStorage.removeItem('sigap_auth')
    localStorage.removeItem('sigap_user')
    router.push('/login')
  }

  function normalizeStatus(s){
    if (!s) return ''
    return s.toString().toUpperCase().replace(/[-\\s]/g,'_')
  }

  async function loadDashboard(){
    setLoading(true)
    try{
      const res = await apiClient('/work-orders?page=1&pageSize=2000')
      const rows = res?.data ?? res ?? []
      const now = Date.now()
      let cnt = { total:0, new:0, assigned:0, ready_to_deploy:0, deployed:0, in_progress:0, completed:0, overdue:0 }
      const up = []
      const od = []
      for (const r of (rows || [])){
        const status = normalizeStatus(r.status || r.raw?.status || r.raw?.doc_status || '')
        cnt.total++
        if (status === 'NEW' || status === 'OPEN') cnt.new++
        if (status === 'ASSIGNED') cnt.assigned++
        if (status === 'READY_TO_DEPLOY') cnt.ready_to_deploy++
        if (status === 'DEPLOYED') cnt.deployed++
        if (status === 'IN_PROGRESS' || status === 'IN-PROGRESS') cnt.in_progress++
        if (status === 'COMPLETED') cnt.completed++

        const sMs = r.start_date ? Date.parse(r.start_date) : null
        const eMs = r.end_date ? Date.parse(r.end_date) : null
        if (eMs && eMs < now && status !== 'COMPLETED') { cnt.overdue++; od.push(r) }
        if (sMs && sMs >= now && sMs <= (now + 7*24*60*60*1000)) up.push(r)
      }

      // sort upcoming by start_date
      up.sort((a,b)=> (Date.parse(a.start_date||'')||0) - (Date.parse(b.start_date||'')||0))
      od.sort((a,b)=> (Date.parse(a.end_date||'')||0) - (Date.parse(b.end_date||'')||0))

      setStats(cnt)
      setUpcoming(up.slice(0,10))
      setOverdueList(od.slice(0,10))
      setLastUpdated(new Date().toLocaleString())
    }catch(err){
      console.error('load dashboard', err)
    }finally{ setLoading(false) }
  }

  return (
    <>
      <Head>
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/twitter-bootstrap/4.6.2/css/bootstrap.min.css" />
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.4.0/css/all.min.css" />
        <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/admin-lte/3.2.0/css/adminlte.min.css" />
      </Head>

      <div className="wrapper">
        <div className="content-wrapper" style={{minHeight: '70vh'}}>
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>Planner Dashboard</h1>
                </div>
                <div className="col-sm-6">
                  <div style={{float:'right'}}>
                    <span style={{marginRight:12}}>Halo, <strong>{user}</strong></span>
                    <button className="btn btn-outline-secondary btn-sm" onClick={logout}>Logout</button>
                  </div>
                </div>
              </div>
            </div>
          </section>

          <section className="content">
            <div className="container-fluid">
              <div className="row">
                <div className="col-lg-3 col-6">
                  <div className="small-box bg-info">
                    <div className="inner">
                      <h3>{stats.total}</h3>
                      <p>Total Work Orders</p>
                    </div>
                    <div className="icon"><i className="fas fa-clipboard-list"></i></div>
                    <a className="small-box-footer" href="#">More info <i className="fas fa-arrow-circle-right"></i></a>
                  </div>
                </div>

                <div className="col-lg-3 col-6">
                  <div className="small-box bg-primary">
                    <div className="inner">
                      <h3>{stats.assigned}</h3>
                      <p>Assigned</p>
                    </div>
                    <div className="icon"><i className="fas fa-user-check"></i></div>
                    <a className="small-box-footer" href="#">More info <i className="fas fa-arrow-circle-right"></i></a>
                  </div>
                </div>

                <div className="col-lg-3 col-6">
                  <div className="small-box bg-warning">
                    <div className="inner">
                      <h3>{stats.in_progress}</h3>
                      <p>In Progress</p>
                    </div>
                    <div className="icon"><i className="fas fa-spinner"></i></div>
                    <a className="small-box-footer" href="#">More info <i className="fas fa-arrow-circle-right"></i></a>
                  </div>
                </div>

                <div className="col-lg-3 col-6">
                  <div className="small-box bg-success">
                    <div className="inner">
                      <h3>{stats.completed}</h3>
                      <p>Completed</p>
                    </div>
                    <div className="icon"><i className="fas fa-check-circle"></i></div>
                    <a className="small-box-footer" href="#">More info <i className="fas fa-arrow-circle-right"></i></a>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-md-4">
                  <div className="card">
                    <div className="card-header">
                      <h3 className="card-title">Upcoming (7 days)</h3>
                      <div className="card-tools"><button className="btn btn-tool" onClick={loadDashboard}><i className="fas fa-sync-alt"></i></button></div>
                    </div>
                    <div className="card-body p-2">
                      <ul className="list-group">
                        {upcoming.length === 0 && <li className="list-group-item">No upcoming work orders</li>}
                        {upcoming.map(w => (
                          <li key={w.id} className="list-group-item list-group-item-action" style={{cursor:'pointer'}} onClick={() => router.push(`/work-orders/${w.id}`)}>
                            <div style={{fontWeight:700}}>{w.doc_no || w.id}</div>
                            <div style={{fontSize:12,color:'#666'}}>{w.asset_name || ''} â€¢ {w.start_date ? new Date(w.start_date).toLocaleString() : ''}</div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="card">
                    <div className="card-header"><h3 className="card-title">Overdue</h3></div>
                    <div className="card-body p-2">
                      <ul className="list-group">
                        {overdueList.length === 0 && <li className="list-group-item">No overdue work orders</li>}
                        {overdueList.map(w => (
                          <li key={w.id} className="list-group-item list-group-item-action" style={{cursor:'pointer'}} onClick={() => router.push(`/work-orders/${w.id}`)}>
                            <div style={{fontWeight:700}}>{w.doc_no || w.id}</div>
                            <div style={{fontSize:12,color:'#666'}}>Due: {w.end_date ? new Date(w.end_date).toLocaleString() : ''}</div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>

                <div className="col-md-4">
                  <div className="card">
                    <div className="card-header"><h3 className="card-title">Gantt Preview</h3></div>
                    <div className="card-body" style={{height:320,overflow:'hidden'}}>
                      <GanttChart pageSize={200} />
                    </div>
                  </div>
                </div>
              </div>

              <div className="row mt-3">
                <div className="col-12 text-muted">Last updated: {lastUpdated || '-'}</div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </>
  )
}
